export const onWatchlistUpdateSuccess = (queryClient: QueryClient) => (result: Watchlist) => {
  queryClient.setQueryData(watchlistQueryKeys.watchlist(result.id), result)
  queryClient.refetchQueries({ queryKey: watchlistQueryKeys.all() })
}

// -------------------
// Network Requests
// -------------------

const getAll = async (params: { owned?: boolean; shared?: "company" }) => {
  const queryString = qs.stringify(params, { addQueryPrefix: true, skipNulls: true })

  const { data } = await axiosClient.get<WatchlistCollection>(
    `${USER_DATA_WATCHLIST_ROOT}${queryString}`,
  )
  return data
}

const create = async (watchlist: CreateWatchlist) => {
  const { data } = await axiosClient.post<Watchlist>(USER_DATA_WATCHLIST_ROOT, watchlist)
  return data
}

export const patchWatchlist = async ({ id, ...fields }: PatchWatchlist) => {
  const { data } = await axiosClient.patch<Watchlist>(`${USER_DATA_WATCHLIST_ROOT}/${id}`, fields)
  return data
}

const dependencies = async (ids: Array<string>) => {
  const { data } = await axiosClient.post<UserDataDependenciesPostResponse>(
    `${USER_DATA_WATCHLIST_ROOT}/dependencies`,
    ids,
  )
  return data
}

type ShareWatchlistSharedBody = {
  company?: {
    organizationId?: string
    permission: SharingPermission
  }
  users?: Array<UserSharingData>
  isPublic?: boolean
}

type ShareWatchlistParams = {
  id: string
  sharedBody: ShareWatchlistSharedBody
}

const shareWatchlist = async ({ id, sharedBody }: ShareWatchlistParams) => {
  const { data } = await axiosClient.post<Watchlist>(
    `${USER_DATA_WATCHLIST_ROOT}/${id}/share`,
    sharedBody,
  )
  return { data, id }
}

const favorite = async (options: { id: string; cameFromSharedLink?: boolean }) => {
  const { data } = await axiosClient.put(`${USER_DATA_WATCHLIST_ROOT}/${options.id}/favorite`)
  return {
    watchlist: data,
    watchlistId: options.id,
    cameFromSharedLink: options.cameFromSharedLink ?? false,
  }
}

const unfavorite = async (watchlistId: string) => {
  const { data } = await axiosClient.delete(`${USER_DATA_WATCHLIST_ROOT}/${watchlistId}/favorite`)
  return { watchlist: data, watchlistId }
}

// -------------------
// Hooks
// -------------------

const useWatchlists = (watchlistIds: Array<string>) => {
  const queryData = watchlistIds.map(watchlistId => {
    return {
      queryKey: watchlistQueryKeys.watchlist(watchlistId),
      queryFn: () => getWatchlistById(watchlistId),
    }
  })

  return useQueries({
    queries: queryData,
  })
}

const useEntitiesFromWatchlists = (expression?: RpqlFilter) => {
  const watchlistIds: Array<string> = expression
    ? getExpressionIds(expression, { types: ["watchlist"] })
    : []
  const watchlistsData = useWatchlists(watchlistIds)
  const entities = watchlistsData.reduce<Array<string>>((acc, watchlist) => {
    if (watchlist.data) {
      return [...acc, ...watchlist.data.items]
    }
    return acc
  }, [])
  return entities
}

const useAllWatchlists = (ownership?: WatchlistOwnership) =>
  useQuery({
    queryKey: watchlistQueryKeys.all(ownership),
    queryFn: () => {
      if (!ownership) {
        return getAll({})
      }

      return ownership === "user" ? getAll({ owned: true }) : getAll({ shared: "company" })
    },
    select: response => response.results.map(mapToISOTimestamp),
    gcTime: 30000,
    staleTime: 30000,
  })

// Create a new watchlist
const useCreateWatchlist = (options?: { enqueueError?: SnackbarEnqueueAction }) => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: create,
    onSuccess: watchlist => {
      watchlistCustomerIo.watchlistApi.create({
        watchlistId: watchlist.id,
        interactionTimestamp: Date.now() * 1000,
        entities: watchlist.items.map(id => ({ id })),
      })
      onWatchlistUpdateSuccess(queryClient)(watchlist)
    },
    onError: error => {
      if (error instanceof AxiosError) {
        options?.enqueueError?.(error?.response?.data?.message ?? "An error occurred")
      }
    },
  })
}

const usePatchWatchlist = (options?: {
  disableSuccessCallback?: boolean
  enqueueError?: SnackbarEnqueueAction
}) => {
  const queryClient = useQueryClient()

  const onMutate = async (watchlist: PatchWatchlist) => {
    queryClient.setQueriesData<WatchlistCollection>(
      { queryKey: watchlistQueryKeys.all() },
      data => {
        if (!data) return data
        return {
          results: data.results.map(item => {
            if (item.id === watchlist.id) {
              return { ...item, ...watchlist }
            }
            return item
          }),
        }
      },
    )
    queryClient.setQueryData<Watchlist | undefined>(
      watchlistQueryKeys.watchlist(watchlist.id),
      data => {
        if (!data) return data
        if (data.id === watchlist.id) {
          return { ...data, ...watchlist }
        }
        return data
      },
    )
  }

  const onError = (error: unknown, watchlist: PatchWatchlist) => {
    if (error instanceof AxiosError) {
      options?.enqueueError?.(error?.response?.data?.message ?? "An error occurred")
    }
    queryClient.invalidateQueries({ queryKey: watchlistQueryKeys.watchlist(watchlist.id) })
    queryClient.invalidateQueries({ queryKey: watchlistQueryKeys.all() })
  }

  const onSuccess = (watchlist: Watchlist, variables: PatchWatchlist) => {
    // If items of the watchlist were updated, clear query preview caches
    // TODO: Check how hard is to only remove the queries that are affected
    if (variables.items) {
      queryClient.invalidateQueries({
        queryKey: [QUERY_KEY_FEATURE_FLAG],
      })
      queryClient.removeQueries({
        queryKey: [CQS_SUMMARY_KEY],
      })
    }
    const updatedFields: Array<"name" | "entities"> = []
    if (variables.name) updatedFields.push("name")
    if (variables.items) updatedFields.push("entities")
    watchlistCustomerIo.watchlistApi.update({
      watchlistId: watchlist.id,
      interactionTimestamp: Date.now() * 1000,
      entities: watchlist.items.map(item => ({ id: item })),
      updatedFields,
      sharedWith: shareCustomerIo.getSharedWith(watchlist.shared),
    })

    if (!options?.disableSuccessCallback) {
      onWatchlistUpdateSuccess(queryClient)(watchlist)
    }
  }

  return useMutation<Watchlist, AxiosError, PatchWatchlist, UpdateContext>({
    mutationFn: patchWatchlist,
    onMutate,
    onError,
    onSuccess,
  })
}

const useWatchlistDependencies = (watchlistId: string | null, savedStatus?: CqsSavedStatus) => {
  const { data, isFetching } = useUserQueries(savedStatus, "user")

  const result = useQuery({
    queryKey: ["watchlists-dependencies", watchlistId],
    queryFn: () => dependencies([watchlistId ?? ""]),
    enabled: !!watchlistId,
    select: inner =>
      inner[watchlistId ?? ""]?.filter(item => {
        if (!data?.results) return false
        return !!data.results?.find(query => query.id === item.id)
      }) ?? [],
  })

  return { ...result, isLoading: result.isFetching || isFetching }
}

const useShareWatchlist = ({ enqueueError }: { enqueueError?: SnackbarEnqueueAction }) => {
  const queryClient = useQueryClient()
  return useMutation({
    mutationFn: shareWatchlist,
    onMutate: ({ id, sharedBody }: ShareWatchlistParams) => {
      // TODO: add company id to shared.company
      queryClient.setQueriesData<WatchlistCollection>(
        { queryKey: watchlistQueryKeys.all() },
        data => {
          if (!data) return data
          return {
            results: data.results.map(item => {
              if (item.id === id) {
                return {
                  ...item,
                  shared: {
                    ...item.shared,
                    ...sharedBody,
                    company: {
                      ...item.shared.company,
                      ...sharedBody.company,
                    },
                  },
                }
              }
              return item
            }),
          }
        },
      )
      queryClient.setQueryData<Watchlist | undefined>(watchlistQueryKeys.watchlist(id), data => {
        if (!data) return data
        if (data.id === id) {
          return {
            ...data,
            shared: {
              ...data.shared,
              ...sharedBody,
              company: {
                ...data.shared.company,
                ...sharedBody.company,
              },
            },
          }
        }
        return data
      })
    },
    onError: (error, data: ShareWatchlistParams) => {
      if (error instanceof AxiosError) {
        enqueueError?.(error?.response?.data?.message ?? "An error occurred")
      }
      queryClient.invalidateQueries({ queryKey: watchlistQueryKeys.watchlist(data.id) })
      queryClient.invalidateQueries({ queryKey: watchlistQueryKeys.all() })
    },
    onSuccess: (params, watchlist) => {
      const { id, data } = params
      watchlistCustomerIo.watchlistApi.update({
        watchlistId: watchlist.id,
        interactionTimestamp: Date.now() * 1000,
        updatedFields: ["sharedWith"],
        entities: null,
        sharedWith: shareCustomerIo.getSharedWith(data.shared),
      })
      queryClient.refetchQueries({ queryKey: watchlistQueryKeys.watchlist(id) })
      queryClient.refetchQueries({ queryKey: watchlistQueryKeys.all() })
    },
  })
}

const useGetWatchlistDependencies = (
  id: string,
  { enqueueError }: { enqueueError: SnackbarEnqueueAction },
) => {
  const { mutateAsync: deleteWatchlist } = useDeleteWatchlist({ enqueueError })

  const { data, isLoading: isLoadingDeps } = useWatchlistDependencies(id, "saved")
  const ids = data ? data.filter(item => item.type === "query").map(item => item.id) : []
  const results = useMultipleSavedQueries(ids)
  const isFetchingQueries = results.filter(item => item.isFetching).length > 0

  const isFetching = isLoadingDeps || isFetchingQueries

  return { deleteWatchlist, isFetching, dependencies: data }
}

const useWatchlistName = (watchlistId: string | undefined) => {
  const { data, isLoading: loading } = useWatchlist(watchlistId)
  const queryClient = useQueryClient()
  const res = queryClient.getQueriesData<WatchlistCollection>({
    queryKey: watchlistQueryKeys.all(),
  })
  const watchlist = res
    .map(([_queryKey, data]) => {
      return data?.results.find(file => file.id === watchlistId)
    })
    .flat()
    .find(file => file?.id === watchlistId)
  const isLoading = watchlist?.name ? false : loading
  return { name: watchlist?.name ?? data?.name, isLoading }
}

const useFavoriteWatchlist = () => {
  const queryClient = useQueryClient()
  return useMutation({
    mutationFn: favorite,
    onMutate: options => {
      queryClient.setQueriesData<WatchlistCollection>(
        { queryKey: watchlistQueryKeys.all() },
        data => {
          if (!data) return data
          return {
            results: data.results.map(item => {
              if (item.id === options.id) {
                return { ...item, isFavorite: true }
              }
              return item
            }),
          }
        },
      )
      queryClient.setQueryData<Watchlist | undefined>(
        watchlistQueryKeys.watchlist(options.id),
        data => {
          if (!data) return data
          if (data.id === options.id) {
            return { ...data, isFavorite: true }
          }
          return data
        },
      )
    },
    onSuccess: async ({
      watchlist,
      watchlistId,
      cameFromSharedLink,
    }: {
      watchlist: Watchlist
      watchlistId: string
      cameFromSharedLink: boolean
    }) => {
      if (watchlist.shared.isPublic) {
        queryClient.invalidateQueries({ queryKey: watchlistQueryKeys.all() })
      }
      watchlistCustomerIo.watchlistApi.favorite({
        watchlistId: watchlistId,
        interactionTimestamp: Date.now() * 1000,
        entities: watchlist.items.map(id => ({ id })),
        updatedFields: [],
        sharedWith: shareCustomerIo.getSharedWith(watchlist.shared),
        cameFromSharedLink,
      })
    },
  })
}

