const getEmptyWatchlist = (
  user: { id?: string; fullName?: string | null; imageUrl?: string | null } | null | undefined,
): Watchlist => {
  return {
    id: "",
    items: [],
    name: defaultSaveName("watchlist"),
    ownerUserId: user?.id ?? "",
    dateCreated: new Date().toISOString().replace("Z", ""),
    lastUpdated: new Date().toISOString().replace("Z", ""),
    lastInteracted: new Date().toISOString().replace("Z", ""),
    ownerName: user?.fullName ?? "",
    ownerImageUrl: user?.imageUrl ?? "",
    shared: { company: { permission: "undefined", organizationId: "" }, users: [] },
    isClientWatchlist: true,
    global: {
      status: "private",
    },
  }
}

const useSaveWatchlistImage = () => {
  const queryClient = useQueryClient()
  return useMutation({
    mutationFn: async ({ id, image }: { id: string; image: string }) => {
      const { data } = await axiosClient.post<z.infer<typeof watchlistSaveImageOutputSchema>>(
        `/user-data/watchlists/${id}/generate-presigned-url?image=true`,
      )
      const headers = {
        "x-amz-server-side-encryption": "AES256",
        "Content-Type": "image/jpeg",
      }
      const [type, base64String] = image.split(";base64,")
      const blob = base64ToBlob(
        base64String,
        type,
        "We couldn't read your file. Please upload an image in a supported format (like JPG, PNG, or GIF).",
      )
      await axiosClient.put(data.url, blob, { headers, withCredentials: false })
      return data
    },
    onSuccess: (data, { id }) => {
      // TODO setQueries
      const imageUrl = `${environment.assetsOrigin}/${data.key}`
      queryClient.setQueryData<Watchlist | undefined>(watchlistQueryKeys.watchlist(id), data => {
        if (!data) return data
        if (data.id === id) {
          return { ...data, imageUrl }
        }
        return data
      })
      queryClient.setQueriesData<WatchlistCollection>(
        { queryKey: watchlistQueryKeys.all() },
        data => {
          if (!data) return data
          return {
            results: data.results.map(item => {
              if (item.id === id) {
                return { ...item, imageUrl }
              }
              return item
            }),
          }
        },
      )
    },
  })
}

const useResetWatchlistImage = () => {
  const queryClient = useQueryClient()
  return useMutation({
    mutationFn: async ({ watchlistId }: { watchlistId: string }) => {
      const { data } = await axiosClient.delete(
        `/user-data/watchlists/${watchlistId}/assets?image=true`,
      )
      return data
    },
    onSuccess: (data, { watchlistId }) => {
      // TODO setQueries
      const imageUrl = undefined
      queryClient.setQueryData<Watchlist | undefined>(
        watchlistQueryKeys.watchlist(watchlistId),
        data => {
          if (!data) return data
          if (data.id === watchlistId) {
            return { ...data, imageUrl }
          }
          return data
        },
      )
      queryClient.setQueriesData<WatchlistCollection>(
        { queryKey: watchlistQueryKeys.all() },
        data => {
          if (!data) return data
          return {
            results: data.results.map(item => {
              if (item.id === watchlistId) {
                return { ...item, imageUrl }
              }
              return item
            }),
          }
        },
      )
    },
  })
}

